  50% {
    transform: rotate(-20deg)scale(1.2);
  }

  100% {
    transform: rotate(-20deg)scale(1);
  }
}

@keyframes move-s {
  0% {
    transform: translate(0);
  }

  100% {
    transform: translate(0, 10dvh);
  }
}

@keyframes move-ne {
  0% {
    transform: translate(0);
  }

  100% {
    transform: translate(5dvw, -3dvh);
  }
}

@keyframes move-cw {
  0% {
    transform: translate(0);
  }

  25% {
    transform: translate(.2dvw, -.3dvh);
  }

  50% {
    transform: translate(.4dvw);
  }

  75% {
    transform: translate(.2dvw, .3dvh);
  }

  100% {
    transform: translate(0);
  }
}

